@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
*{margin: 0; padding: 0}
ol, ul { list-style: none;}
a { text-decoration:none; outline:none}
a:hover { cursor:pointer; text-decoration:none; text-indent:0}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section { display: block;}


.brand-section{
    padding: 50px;
    overflow: hidden;
}

.heading{
    font-family: 'Pacifico', cursive;
    color: #0073b1;
    text-align: center;
    font-size: 40px;
    letter-spacing: 4px;
    margin-bottom: 50px;
}

.brand-area{
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    padding: 10px;
    width: auto;
}

.brand{
    width: 260px;
    height: 150px;
    margin-right: 50px;
    display: inline-block;
    background: #0073b1;
    border-radius: 20px;
    line-height: 150px;
    text-align: center;
    color: #ffffff;
    font-family: Arial, serif;
    font-size: 30px;
}

.brand-area .brand:last-child{
    margin-right: 0;
}

@media only screen and (max-width: 600px) {

    .brand-area{
        overflow-x: hidden;
        height: 320px;
        width: 300px;
        margin: 0 auto;
        overflow-y: scroll;
        text-align: center;
    }

    .brand{
        display: block;
        margin-right: 0;
        margin-bottom: 20px;
    }

    .brand-area .brand:last-child{
        margin-bottom: 0;
    }

}

*{
    box-sizing: unset;
}

.company-section{
    padding: 50px;
    overflow: hidden;
}

.heading{
    font-family: 'Pacifico', cursive;
    color: #0073b1;
    text-align: center;
    font-size: 40px;
    letter-spacing: 4px;
    margin-bottom: 50px;
}

.text{
    color: #000000;
    font-family: "Lucida Console", serif;
    font-size: 30px;
    text-align: center;
}

.goals{
    overflow: hidden;
    margin: 50px auto 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.goal{
    border-radius: 20px;
    height: 240px;
    background-color: #0073b1;
    padding-top: 5px;
    padding-bottom: 30px;
}

.section{
    width: 260px;
    height: 240px;
}

.delivery{
    background: url(data:image/png;base64,) center center no-repeat;
}

.cart{
    background: url(data:image/png;base64,) center center no-repeat;
}

.customer{
    background: url(/static/media/customer.4ccaa1ea.png) center center no-repeat;
}

.goal-title{
    text-align: center;
    font-size: 20px;
    font-family: "Lucida Console", serif;
    color: #ffffff;
    margin-top: 5px;
}

@media only screen and (max-width: 950px) {

    .goals{
        max-width: 300px;
        flex-direction: column;
        margin-left: auto;
        margin-right: auto;
    }

    .goal{
        margin-bottom: 20px;
        width: 260px;
    }

    .goals .goal:last-child{
        margin-bottom: 0;
    }

}

.content {
    margin-top: 80px
}

.ratings {
    background-color: #ffff;
    padding: 54px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: 0px 10px 10px #E0E0E0
}
.Card{
    box-shadow: 0px 10px 10px #E0E0E0
}
.product-rating {
    font-size: 50px
}

